@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@500;700&display=swap");
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
* {
  margin: 0;
  padding: 0;
  font: inherit;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

/* Set core body defaults */
body {
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture,
svg {
  width: 100%;
  display: block;
}

ul {
  list-style-type: none;
}

table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  text-align: left;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

details > summary {
  list-style: none;
}

details > summary::-webkit-details-marker {
  display: none;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --d-g-blue: 217 19% 35%;
  --d-blue: 214 17% 51%;
  --gray: 212 23% 69%;
  --li-gray: 210 46% 95%;
  --white: 0 0% 100%;
  --black: 0 0% 8%;
  --selection-bg: 277 100% 50%;
}

/**
 * ### Font family 
 */
/**
 * ### Size variables
 */
/**
 * ### Font weight
 */
::selection {
  background-color: hsl(var(--selection-bg));
  color: hsl(var(--white));
}

::-moz-selection {
  background-color: hsl(var(--selection-bg));
  color: hsl(var(--white));
}

html {
  font-size: 100%;
  font-family: "Manrope", sans-serif;
}

body {
  background-color: hsl(var(--li-gray));
  font-size: 0.75rem;
}

p:not([class]) {
  max-width: 45ch;
}

/**
 * Colors
 */
.bg-white {
  background-color: hsl(var(--white));
}

.clr-white {
  color: hsl(var(--white));
}

.bg-black {
  background-color: hsl(var(--black));
}

/**
 * Position
 */
.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

/**
 * ### With container
 */
.container {
  --max-width: 1440px;
  --container-padding: 1rem;
  width: min(var(--max-width), 100% - var(--container-padding) * 0);
  margin-inline: auto;
}

/**
 * ### Flex 
 */
.flex {
  display: flex;
}

.flex-dir-col {
  flex-direction: column;
}

.justify-cont-betw {
  justify-content: space-between;
}

/**
 * ### Grid 
 */
.grid-columns {
  display: grid;
  align-content: space-around;
}
@media (min-width: 52.5em) {
  .grid-columns {
    grid-auto-flow: column;
    grid-auto-columns: 1fr;
  }
}

/**
 * ### Gap 
 */
.gap-1 {
  gap: 1rem;
}

.gap-2 {
  gap: 2rem;
}

.gap-3 {
  gap: 3rem;
}

/**
 * Float
 */
.float-l {
  float: left;
}

.float-r {
  float: right;
}

/**
 * ### Align items center 
 */
.align-itm-center {
  align-items: center;
}

/**
 * ### Justify content center , start
 */
.justify-cont-center {
  justify-content: center;
}

.justify-cont-start {
  justify-content: start;
}

/**
 * ### Justify items center , start
 */
.justify-item-center {
  justify-items: center;
}

.justify-items-start {
  justify-items: start;
}

/**
 * ### Text  center 
 */
.text-align-center {
  text-align: center;
}

/**
 * Flow -Creating an upper edge for all descendants except the first.
 * inline style --> style="--flow-spacer: 1.5rem"
 */
:where(.flow :not(:first-child)) {
  margin-top: var(--flow-spacer, 1em);
}

/**
 * ### Margin Padding position
 */
.margin-bottom-500 {
  margin-block-end: 1.5rem;
}

/**
 * Padding top and bottom
 */
.padding-block-400 {
  padding-block: 1rem;
}

.padding-block-500 {
  padding-block: 1.5rem;
}

.padding-block-600 {
  padding-block: 2rem;
}

.padding-block-700 {
  padding-block: 3rem;
}

.padding-block-900 {
  padding-block: 5rem;
}

.padding-lr-400 {
  padding-inline: 1rem;
}

.padding-lr-500 {
  padding-inline: 1.5rem;
}

.padding-lr-600 {
  padding-inline: 2rem;
}

.padding-lr-800 {
  padding-inline: 4rem;
}

/**
 * ### Font Size
 */
.fs-400 {
  font-size: 1rem;
}

.fs-600 {
  font-size: 2rem;
}

.fs-700 {
  font-size: 3rem;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap; /* added line */
  border: 0;
}

.btn {
  display: inline-flex;
  border: 0;
  padding: 1rem 1.375rem;
  text-decoration: none;
  font-family: sans-serif;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1;
  cursor: pointer;
  text-align: center;
  transition: background 250ms ease-in-out, transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
}

.btn:focus {
  outline: none;
  outline-offset: -4px;
}

.btn:active {
  transform: scale(0.99);
}

body {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  display: flex;
  flex-direction: column;
  margin: auto;
  border-radius: 0.75rem;
  max-width: 21.875rem;
  background-color: hsl(var(--white));
}
@media (min-width: 52.5em) {
  .card {
    flex-direction: row;
    max-width: 50rem;
  }
}

.card-figure {
  max-width: 350px;
  max-height: 320px;
  border-top-left-radius: 0.75rem;
  border-top-right-radius: 0.75rem;
  overflow: hidden;
}
@media (min-width: 52.5em) {
  .card-figure {
    border-top-left-radius: 0.75rem;
    border-bottom-left-radius: 0.75rem;
    border-top-right-radius: 0rem;
  }
}

.card-img {
  width: 430px;
  height: 350px;
}

.card-info {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-inline: 1.875rem;
  padding-block: 1.875rem;
  align-items: center;
  max-width: 50ch;
}
@media (min-width: 52.5em) {
  .card-info {
    flex-direction: column;
    align-items: initial;
    max-width: 60ch;
  }
}

h2 {
  font-size: 1.375rem;
  color: hsl(var(--d-g-blue));
  font-weight: 700;
  max-width: 45ch;
}

p {
  font-size: 0.875rem;
  color: hsl(var(--d-blue));
}

.card-profile {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-top: 1.875rem;
}

.card-profile-figure {
  display: flex;
  align-items: center;
}

.profile-img {
  width: 40px;
  height: 40px;
  border-radius: 100vmax;
  margin-right: 1.25rem;
}

.profile-name {
  font-weight: 700;
  font-size: 0.8125rem;
}

.profile-date {
  color: hsl(var(--gray));
  font-weight: 700;
}

.share-box {
  max-width: 55ch;
  display: none;
  gap: 10px;
  padding: 2.1875rem 3.75rem;
  justify-content: flex-start;
  align-items: center;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  text-transform: uppercase;
  background-color: hsl(var(--d-g-blue));
  color: hsl(var(--gray));
  letter-spacing: 6px;
  border-radius: 0.5rem;
  animation-duration: 0.55s;
}
@media (min-width: 52.5em) {
  .share-box {
    justify-content: center;
    max-width: 40ch;
    padding: 0.9375rem 2.5rem;
    left: unset;
    bottom: 7rem;
    right: -4.5rem;
    float: right;
  }
  .share-box::after {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    bottom: -10px;
    border-style: solid;
    border-width: 10px 10px 0 10px;
    border-color: hsl(var(--d-g-blue)) transparent transparent transparent;
  }
}

.show-box {
  display: flex;
}

i {
  color: hsl(var(--white));
  font-size: 1rem;
}

button {
  background-color: hsl(var(--gray));
  border-radius: 100%;
  width: 40px;
  height: 40px;
  border: 0;
  line-height: 0;
  z-index: 99;
}

button i {
  color: hsl(var(--d-g-blue));
}

button i:active,
button i:focus,
button i:hover {
  color: hsl(var(--white));
}